<template>
  <div ref="wrapper">
    <slot></slot>
  </div>
</template>

<script>
  import BScroll from '@better-scroll/core'
  import Pullup from '@better-scroll/pull-up'
  import PullDown from '@better-scroll/pull-down'

  BScroll.use(PullDown)
  BScroll.use(Pullup)

  export default {
    name: "Scroll",
    props: {
      /**
       * 1 scroll
       * 2 scroll
       * 3 scrollswipescroll
       */
      probeType: {
        type: Number,
        default: 1
      },
      /**
       * click
       */
      click: {
        type: Boolean,
        default: true
      },
      /**
       * 
       */
      scrollX: {
        type: Boolean,
        default: false
      },
      /**
       * 
       */
      listenScroll: {
        type: Boolean,
        default: false
      },
      /**
       * 
       */
      data: {
        type: Array,
        default: null
      },
      /**
       * 
       */
      pullup: {
        type: Boolean,
        default: false
      },
      /**
       * 
       */
      pulldown: {
        type: Boolean,
        default: false
      },
      /**
       * 
       */
      beforeScroll: {
        type: Boolean,
        default: false
      },
      /**
       * scroll
       */
      refreshDelay: {
        type: Number,
        default: 20
      }
    },
    mounted() {
      // DOMbetter-scroll
      setTimeout(() => {
        this._initScroll()
      }, 20)
    },
    methods:{
      _initScroll() {
        if (!this.$refs.wrapper) {
          return
        }
        // better-scroll
        this.scroll = new BScroll(this.$refs.wrapper, {
          probeType: this.probeType,
          click: this.click,
          scrollX: this.scrollX,
          mouseWheel:true,
          disableMouse: false,
          disableTouch: false,

          //pull-down
          pullDownRefresh: {
            threshold: 70,
            stop: 56
          },
          //pull-up
          pullUpLoad: {
            threshold: 70,
          }
        })

        // 
        if (this.listenScroll) {
          this.scroll.on('scroll', (pos) => {
            this.$emit('scroll', pos)
          })
        }

        // 
        if (this.pullup) {
          this.scroll.on('scrollEnd', () => {
            // 
            if (this.scroll.y <= (this.scroll.maxScrollY + 50)) {
              this.$emit('scrollEnd')
            }
          })
        }

        //pullingUp
        if(this.pullup){
          this.scroll.on('pullingUp',() => {
            this.$emit('pullingUp')
          })
        }

        // 
        if (this.pulldown) {
          this.scroll.on('touchEnd', (pos) => {
            // 
            if (pos.y > 50) {
              this.$emit('toucuend')
            }
          })
        }

        //pullingDown
        if(this.pulldown){
          this.scroll.on('pullingDown',() => {
            this.$emit('pullingDown')
          })
        }

        // 
        if (this.beforeScroll) {
          this.scroll.on('beforeScrollStart', () => {
            this.$emit('beforeScroll')
          })
        }
      },
      disable() {
        // better-scrolldisable
        this.scroll && this.scroll.disable()
      },
      enable() {
        // better-scrollenable
        this.scroll && this.scroll.enable()
      },
      refresh() {
        // better-scrollrefresh
        this.scroll && this.scroll.refresh()
      },
      scrollTo() {
        // better-scrollscrollTo
        this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments)
      },
      scrollToElement() {
        // better-scrollscrollToElement
        this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)
      },

      /* pulluppulldownfinish */
      finishPullDown(){
        this.scroll.finishPullDown();
      },
      finishPullUp(){
        this.scroll.finishPullUp();
      }
    },
    watch: {
      // refreshDelayrefresh
      data() {
        setTimeout(() => {
          this.refresh()
        }, this.refreshDelay)
      }
    }
  }
</script>

<style scoped lang="less">

</style>